import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, Tabbar, Epic, TabbarItem, Panel, PanelHeader, Group, Div, Button, FormLayout, Input } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Icon28HelpOutline from '@vkontakte/icons/dist/28/help_outline';
import Icon24MoneyTransfer from '@vkontakte/icons/dist/24/money_transfer';
import Icon24MoneyCircle from '@vkontakte/icons/dist/24/money_circle';
import Icon24Home from '@vkontakte/icons/dist/24/home';
import swal from 'sweetalert';

import Home from './panels/Home';
import Persik from './panels/Persik';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			fetchedUser: null,
			userInfo: null,
			token: null,
			activeStory: 'home',
			swal: null,
			withdrawvalue: 0,
			monetka: 0,
		};
		this.updateBalance = this.updateBalance.bind(this);
		this.onStoryChange = this.onStoryChange.bind(this);
		this.handleChange = this.handleChange.bind(this);
    this.handleWithdraw = this.handleWithdraw.bind(this);

		this.handleChangeMonetka = this.handleChangeMonetka.bind(this);
		this.handleMonetka = this.handleMonetka.bind(this);
	}

  updateBalance(){
		fetch(`https://typokekay.voloshinskii.ru/getuserinfo/${this.state.token}`)
			.then(res => res.json())
			.then(json => this.setState({ userInfo: json }));
	}

	onStoryChange(e) {
    this.setState({activeStory: e.currentTarget.dataset.story })
  }

	handleChange(event) {
    this.setState({withdrawvalue: event.target.value});
  }

	handleWithdraw(event) {
		fetch(`https://typokekay.voloshinskii.ru/withdraw/${this.state.token}?amount=${this.state.withdrawvalue}`)
			.then(res => res.json())
			.then(json => swal(json.ok ? "Успешно!" : "Ошибка", json.desc, json.ok ? "success" : "error"));
		this.updateBalance();
  }

	handleChangeMonetka(event) {
		this.setState({monetka: event.target.value});
	}

	handleMonetka(event) {
		fetch(`https://typokekay.voloshinskii.ru/monetka/${this.state.token}?bet=${this.state.monetka}`)
			.then(res => res.json())
			.then(json => swal(json.ok ? "Победа!" : "Ошибка", json.desc, json.ok ? "success" : "error"));
		this.updateBalance();
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
					case 'VKWebAppAccessTokenReceived':
						this.setState({ token: e.detail.data.access_token });
						fetch(`https://typokekay.voloshinskii.ru/getuserinfo/${e.detail.data.access_token}`)
				      .then(res => res.json())
				      .then(json => this.setState({ userInfo: json }));
						break;

				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		connect.send("VKWebAppGetAuthToken", {"app_id": 6935354, "scope": ""});

	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	ffiixx = (e) => {
		this.setState({ activeStory: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<Epic activeStory={this.state.activeStory} tabbar={
        <Tabbar>
					<TabbarItem
						onClick={this.onStoryChange}
						selected={this.state.activeStory === 'home'}
						data-story="home"
						text="Главная"
					><Icon24Home/></TabbarItem>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'deposit'}
            data-story="deposit"
            text="Депозит"
          ><Icon24MoneyCircle/></TabbarItem>
					<TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'withdraw'}
            data-story="withdraw"
            text="Вывод"
          ><Icon24MoneyTransfer/></TabbarItem>
					<TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'help'}
            data-story="help"
            text="Помощь"
          ><Icon28HelpOutline/></TabbarItem>
        </Tabbar>
      }>
				<View id="deposit" activePanel="deposit">
					<Panel id="deposit">
						<PanelHeader>Депозит</PanelHeader>
						<Group>
							<Div>
								Для того, чтобы пополнить баланс на нашем боте, переведите свои токены на этот аккаунт: https://vk.com/id540462074. После этого они автоматически начислятся на аккаунт казино<br/><br/>
								<a style={{textDecoration:"none"}} href="https://vk.com/coin#t540462074"><Button size="xl" level="secondary">Перейти в VKCOIN</Button></a>
							</Div>
						</Group>
					</Panel>
				</View>
				<View id="withdraw" activePanel="withdraw">
					<Panel id="withdraw" theme="white">
					<PanelHeader>Вывод</PanelHeader>
          <FormLayout>
            <Input
              type="text"
              top="Сумма для вывода"
              name="email"
							placeholder="1 000 000"
							onChange={this.handleChange}
            />
						<Button size="xl" onClick={this.handleWithdraw} level="secondary">Вывести</Button>
						<p style={{marginLeft: 15, fontWeight: "bold"}}>Ваш баланс: {this.state.userInfo && this.state.userInfo.balance && this.state.userInfo.balance.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")} коинов</p>
						<Button onClick={this.updateBalance} size="xl" level="secondary">Обновить баланс</Button>
					</FormLayout>
					</Panel>
				</View>
				<View id="help" activePanel="help">
					<Panel id="help">
						<PanelHeader>Помощь</PanelHeader>
						<Group>
							<Div>
								Наше сообщество: https://vk.com/vkccasino
							</Div>
							</Group>
						<Group>
							<Div>
								VKCOIN Casino - проект, благодаря которому Вы сможете преумножить свои VK коины и попасть в ТОП-100!
							</Div>
							</Group>
							<Group>
				      <Div>
				        Если вдруг что-то сломалось ( что довольно крайне редко происходит ), Вы можете написать мне в ЛС<br/><br/>
								<a style={{textDecoration:"none"}} href="https://vk.com/voloshinskii"><Button size="xl" level="secondary">Перейти</Button></a>
				      </Div>
				    </Group>
					</Panel>
				</View>
				<View id="home" activePanel="home">
					<Home id="home" userInfo={this.state.userInfo} fetchedUser={this.state.fetchedUser} activePanel="home" go={this.ffiixx} updateBalance={this.updateBalance} />
				</View>
				<View id="monetka" activePanel="monetka">
				<Panel id="monetka" theme="white">
					<PanelHeader>Монетка</PanelHeader>
					<FormLayout>
						<Input
							type="text"
							top="Ставка"
							name="email"
							placeholder="1 000 000"
							onChange={this.handleChangeMonetka}
						/>
						<Button size="xl" onClick={this.handleMonetka} level="secondary">Сыграть</Button>
						<p style={{marginLeft: 15, fontWeight: "bold"}}>Ваш баланс: {this.state.userInfo && this.state.userInfo.balance && this.state.userInfo.balance.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")} коинов</p>
						<Button onClick={this.updateBalance} size="xl" level="secondary">Обновить баланс</Button>
					</FormLayout>
					</Panel>
				</View>
      </Epic>
		);
	}
}

export default App;
