import React from 'react';
import PropTypes from 'prop-types';
import { Panel, ListItem, Button, Group, Div, Avatar, PanelHeader, Header, Gallery } from '@vkontakte/vkui';

const itemStyle = {
flexShrink: 0,
width: 80,
height: 220,
display: 'flex',
flexDirection:
'column',
alignItems: 'center',
fontSize: 12
};

const Home = ({ id, go, fetchedUser, userInfo, updateBalance }) => (
	<Panel theme="white" id={id}>
		<p style={{fontWeight: "bold", color: "grey", fontSize: 26, textAlign: 'center'}}>Счёт в казино</p>
		{userInfo && userInfo.balance && <p style={{fontWeight: "bold", color: "black", fontSize: 36, textAlign: 'center'}}>{userInfo.balance.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")}</p>}
		<Button onClick={updateBalance} size="xl" level="secondary">Обновить баланс</Button>

		<Group title="Игры">
      <Gallery
        slideWidth="90%"
        align="center"
        style={{ height: 150 }}
      >
        <div onClick={go} data-to="monetka" style={{ backgroundColor: 'var(--accent)', padding:10}}><p style={{fontWeight: "bold", color: "white", fontSize: 26}}>Монетка</p></div>
      </Gallery>
  </Group>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
